/* src/components/AppointmentBookingModal.css */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 80px;
  z-index: 9999;

  /* Reset host styles */
  all: initial;
  font-family: Arial, sans-serif;
  box-sizing: border-box;
}

.modal-overlay *,
.modal-overlay *::before,
.modal-overlay *::after {
  all: unset;
  box-sizing: border-box;
  font-family: inherit;
}

.modal-content {
  background: white;
  padding: 24px;
  border-radius: 10px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 10000;
  color: #000;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  color: #000;
  background: none;
  border: none;
}

.modal-error {
  color: red;
  margin: 10px 0;
}

.slot-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 10px;
  margin: 10px 0;
}

.slot,
.slot-selected {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  text-align: center;
  color: #000;
}

.slot-selected {
  background: #007bff;
  color: #fff;
  border-color: #0056b3;
}

form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 12px;
}

input,
textarea {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  width: 100%;
}

textarea {
  resize: vertical;
  min-height: 60px;
}

button {
  padding: 12px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 15px;
}

button:disabled {
  background: #ccc;
  cursor: not-allowed;
}
